import { Meta, Title } from '@angular/platform-browser';
import { EcommerceOrderRefund } from '@biz-nest/commerce-client';
import {
  AppNameService,
  EventbusService, ListComposeAdminPanel,
  MainBarTitleService,
  scrollToTop, WindowService
} from '@front-shared-lib';
import { ToastrService } from 'ngx-toastr';

export abstract class EcommerceOrderRefundListBase {
  posting = false;
  cleanup?: () => void;
  listCompose = new ListComposeAdminPanel<EcommerceOrderRefund>(
    EcommerceOrderRefund,
    this.eventbus,
    this.windowService,
    this.toastr
  );

  actions = [
    {
      label: 'View',
      icon: 'visibility',
      event: (ecommerceOrderRefund: EcommerceOrderRefund) => this
        .viewEcommerceEcommerceOrderRefund(ecommerceOrderRefund)
    },
    {
      label: 'Update',
      icon: 'edit',
      event: (ecommerceOrderRefund: EcommerceOrderRefund) => this.updateEcommerceOrderRefund(ecommerceOrderRefund)
    }
  ];


  constructor(
    protected windowService: WindowService,
    protected toastr: ToastrService,
    protected eventbus: EventbusService,
    protected apNme: AppNameService,
    public mainBarTitle: MainBarTitleService,
    protected metaTagService: Meta,
    protected titleService: Title
  ) { }

  init(): void {
    this.listCompose.getDataArr();
    this.mainBarTitle.title = 'EcommerceOrder Refund List';
    this.titleService.setTitle(this.mainBarTitle.title + ' - ' + this.apNme.name);
    this.metaTagService.updateTag({
      name: 'description',
      content: `Efficiently manage and comprehensively monitor your order refund,
      empowering you to maintain financial clarity and make informed decisions
      regarding your fiscal responsibilities and opportunities for savings.`
    });

    scrollToTop(this.windowService);
  }


  destroy(): void {
    this.listCompose.cleanup?.();
  }

  abstract viewEcommerceEcommerceOrderRefund(ecommerceOrderRefund: EcommerceOrderRefund): void;

  abstract updateEcommerceOrderRefund(ecommerceOrderRefund: EcommerceOrderRefund): void;
}
