import { Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute, Router, RouterModule } from '@angular/router';
import {
  EventbusService,
  MainBarTitleService,
  openClose,
  ViewEcommerceOrderRefundBase
} from '@front-shared-lib';

@Component({
  selector: 'app-view-ecommerce-order-refund',
  standalone: true,
  imports: [
    RouterModule
  ],
  animations: [
    openClose
  ],
  templateUrl: './view-ecommerce-order-refund.component.html',
  styleUrl: './view-ecommerce-order-refund.component.scss'
})
export class ViewEcommerceOrderRefundComponent
  extends ViewEcommerceOrderRefundBase
  implements OnInit, OnDestroy {
  constructor(
    protected override router: Router,
    protected override route: ActivatedRoute,
    protected override eventbus: EventbusService,
    public override mainBarTitle: MainBarTitleService
  ) {
    super(router, route, eventbus, mainBarTitle);
  }

  ngOnInit(): void {
    this.init();
  }

  ngOnDestroy(): void {
    this.destroy();
  }

  closeModal(): void {
    this.eventbus.generalEvent$.next({
      type: 'closeViewEcommerceOrderRefundModal'
    });
  }
}

