import { Component, OnDestroy, OnInit } from '@angular/core';
import { RouterModule } from '@angular/router';

import { Meta, Title } from '@angular/platform-browser';
import { EcommerceOrderRefund } from '@biz-nest/commerce-client';
import {
  AppNameService,
  EcommerceOrderRefundListBase,
  EventbusService,
  MainBarTitleService,
  WindowService
} from '@front-shared-lib';
import { ToastrService } from 'ngx-toastr';
import {
  AdminDateFilterFieldComponent
} from '../../../../misc/admin-date-filter-field/admin-date-filter-field.component';
import { AdminModalContainerComponent } from '../../../../misc/admin-modal-container/admin-modal-container.component';
import { AdminPageContentComponent } from '../../../../misc/admin-page-content/admin-page-content.component';
import { AdminSearchFieldComponent } from '../../../../misc/admin-search-field/admin-search-field.component';
import { AdminTableComponent } from '../../../../misc/admin-table/admin-table.component';
import { AdminTopBarComponent } from '../../../../misc/admin-top-bar/admin-top-bar.component';
import {
  AddUpdateEcommerceOrderRefundComponent
} from '../add-update-ecommerce-order-refund/add-update-ecommerce-order-refund.component';
import {
  ViewEcommerceOrderRefundComponent
} from '../view-ecommerce-order-refund/view-ecommerce-order-refund.component';

@Component({
  selector: 'app-ecommerce-order-refund-list',
  standalone: true,
  imports: [
    RouterModule,
    AdminTableComponent,
    AdminPageContentComponent,
    AdminSearchFieldComponent,
    AdminDateFilterFieldComponent,
    AdminTopBarComponent,
    AdminModalContainerComponent,
    ViewEcommerceOrderRefundComponent,
    AddUpdateEcommerceOrderRefundComponent
  ],
  templateUrl: './ecommerce-order-refund-list.component.html',
  styleUrl: './ecommerce-order-refund-list.component.scss'
})
export class EcommerceOrderRefundListComponent
  extends EcommerceOrderRefundListBase
  implements OnInit, OnDestroy {
  viewModalOptions = {
    showModal: false,
    modalTitle: 'View EcommerceOrder Refund',
    modalMsg: 'Are you sure you want to view this item?',
    modalCancelButton: true,
    modalCancelText: 'Cancel',
    modalConfirmText: 'View',
    modalConfirmButton: true
  };

  updateModalOptions = {
    showModal: false,
    modalTitle: 'Update EcommerceOrder Refund',
    modalMsg: 'Are you sure you want to update this item?',
    modalCancelButton: true,
    modalCancelText: 'Cancel',
    modalConfirmText: 'Update',
    modalConfirmButton: true
  };

  ecommerceOrderRefund?: EcommerceOrderRefund;

  constructor(
    protected override windowService: WindowService,
    protected override toastr: ToastrService,
    protected override eventbus: EventbusService,
    protected override apNme: AppNameService,
    public override mainBarTitle: MainBarTitleService,
    protected override metaTagService: Meta,
    protected override titleService: Title
  ) {
    super(windowService, toastr, eventbus, apNme, mainBarTitle, metaTagService, titleService);
  }

  ngOnInit(): void {
    this.init();
  }

  ngOnDestroy(): void {
    this.destroy();
  }

  viewEcommerceEcommerceOrderRefund(ecommerceOrderRefund: EcommerceOrderRefund) {
    this.eventbus.generalEvent$.next({
      type: 'oneItem',
      data: ecommerceOrderRefund
    });
    this.viewModalOptions.showModal = true;
    this.ecommerceOrderRefund = ecommerceOrderRefund;
  }

  updateEcommerceOrderRefund(ecommerceOrderRefund: EcommerceOrderRefund) {
    this.eventbus.generalEvent$.next({
      type: 'addUpdateEcommerceOrderRefund',
      data: {
        ecommerceOrderId: ecommerceOrderRefund.ecommerceOrder,
        ecommerceUserId: ecommerceOrderRefund.commerceUser,
        ecommerceOrderRefund,
        status: ecommerceOrderRefund.status
      }
    });
    this.updateModalOptions.showModal = true;
    this.ecommerceOrderRefund = ecommerceOrderRefund;
  }

  toggleModalView() {
    this.viewModalOptions.showModal = !this.viewModalOptions.showModal;
  }

  toggleModalUpdate() {
    this.updateModalOptions.showModal = !this.updateModalOptions.showModal;
  }
}
